import { CurrencyPipe, NgClass } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CompanySubscription } from '@biz-nest/commerce-client';
import { FrontendLogger, IfilterProps } from '@biz-nest/core-client';
import { CurrencyService, EventbusService } from '@front-shared-lib';

@Component({
  selector: 'app-subscription-cards',
  standalone: true,
  imports: [
    RouterModule,
    CurrencyPipe,
    NgClass
  ],
  templateUrl: './subscription-cards.component.html',
  styleUrl: './subscription-cards.component.scss'
})
export class SubscriptionCardsComponent
implements OnInit {
  totalSubscriptionsAmount = 0;
  active = 0;
  expired = 0;
  totalSubscriGrowth = 0;
  recent = 0;

  constructor(
    public currencyService: CurrencyService,
    private eventbus: EventbusService
  ) {}

  ngOnInit(): void {
    this.getTotal();
    this.getActive();
    this.getExpired();
    this.getRecent();
    this.getTotalCompanyGrowthDiff();
  }

  async getTotal() {
    try {
      const analytics = await CompanySubscription.getCompanySubscriptionAnalytcis({});

      if (!analytics) {
        return;
      }

      this.totalSubscriptionsAmount = analytics.totalAmount;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getActive() {
    try {
      const { count } = await CompanySubscription.filterAll({
        propFilter: {
          active: true
        },
        returnEmptyArray: true
      });

      this.active = count;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }


  async getExpired() {
    try {
      const { count } = await CompanySubscription.filterAll({
        propFilter: {
          active: false
        },
        returnEmptyArray: true
      });

      this.expired = count;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getRecent() {
    try {
      const { count } = await CompanySubscription.filterAll({
        comparisonFilter: [
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth(), 0),
            operator: 'gte'
          }
        ],
        returnEmptyArray: true
      });

      this.recent = count;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }


  async getTotalCompanyGrowthDiff() {
    try {
      const lastMonth = await CompanySubscription.filterAll({
        comparisonFilter: [
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth() - 1, 1),
            operator: 'gte'
          },
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth(), 0),
            operator: 'lte'
          }
        ]
      });

      const thisMonth = await CompanySubscription.filterAll({
        comparisonFilter: [
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth(), 1),
            operator: 'gte'
          },
          {
            field: 'createdAt',
            fieldValue: new Date(new Date().getFullYear(), new Date().getMonth() + 1, 0),
            operator: 'lte'
          }
        ]
      });

      this.totalSubscriGrowth = ((thisMonth.count - lastMonth.count) / (lastMonth.count || 1) * 100) || 0;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  filterBy(filter: IfilterProps) {
    this.eventbus.generalEvent$.next({
      type: 'filterBy',
      data: {
        propFilter: filter
      }
    });
  }
}
