import { Component, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { RouterModule } from '@angular/router';
import {
  AppNameService, MainBarTitleService,
  WindowService,
  scrollToTop
} from '@front-shared-lib';
import { AdminPageContentComponent } from '../../misc/admin-page-content/admin-page-content.component';
import { AdminTopBarComponent } from '../../misc/admin-top-bar/admin-top-bar.component';
import { SubscriptionCardsComponent } from '../subscription-cards/subscription-cards.component';
import { SubscriptionListSharableComponent } from '../subscription-list-sharable/subscription-list-sharable.component';

@Component({
  selector: 'app-subscription-list',
  standalone: true,
  imports: [
    RouterModule,
    SubscriptionCardsComponent,
    AdminPageContentComponent,
    AdminTopBarComponent,
    SubscriptionListSharableComponent
  ],
  templateUrl: './subscription-list.component.html',
  styleUrl: './subscription-list.component.scss'
})
export class SubscriptionListComponent
implements OnInit {
  cleanup?: () => void;


  constructor(
    private windowService: WindowService,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private metaTagService: Meta,
    private titleService: Title
  ) { }

  ngOnInit(): void {
    this.mainBarTitle.title = 'Subscriptions - Manage Subscription Plans';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: 'Easily manage and view your subscriptions for effective service tracking and organization.'
    });
    scrollToTop(this.windowService);
  }
}
